<style scoped lang="less">
    .page {
        text-align: left;
        padding: 30px;
    }

    .pagination {
        margin-top: 20px;
        width: 100%;
        text-align: center;
    }
</style>

<template>
    <div class="page">
        <el-row class="header">
            <el-col class="filter-label" :span="2">规则编号:</el-col>
            <el-col :span="4">
                <el-input class="filter-match" v-model="data.id" placeholder="请输入"></el-input>
            </el-col>
            <el-col class="filter-label" :span="2">标题:</el-col>
            <el-col :span="4">
                <el-input class="filter-match" v-model="data.title" placeholder="请输入"></el-input>
            </el-col>
            <el-col class="filter-btn" :span="6">
                <el-button icon="el-icon-search" type="primary" @click="loadData(1)">查询</el-button>
                <el-button icon="el-icon-refresh" @click="ref()">重置</el-button>
                <el-button icon="el-icon-plus" @click="add" type="primary">新建</el-button>
            </el-col>
        </el-row>

        <el-table
                :data="res.data"
                stripe
                style="width: 100%">
            <el-table-column
                    prop="id"
                    sortable
                    label="编号">
            </el-table-column>
            <el-table-column
                    sortable
                    label="状态">
                <template slot-scope="scope">
                    <span>{{scope.row.status===1?'启用':'未启用'}}</span>
                </template>
            </el-table-column>

            <el-table-column
                    prop="title"
                    label="标题">
            </el-table-column>
            <el-table-column
                    label="图片">
                <template slot-scope="scope">
                    <img @click="imgPreview('http://alliance.fh.81youxi.com'+scope.row.picture)" style="margin-left: 10px"
                         :src="'http://alliance.fh.81youxi.com'+scope.row.picture" width="150"/>
                </template>
            </el-table-column>
            <el-table-column
                    prop="pubtime"
                    label="发布时间">
            </el-table-column>

            <el-table-column
                    label="操作">
                <template slot-scope="scope">
                    <el-button @click="handleClick(scope.row)" type="text" size="small">编辑</el-button>
                    <el-button type="text" size="small" @click="del(scope)">删除</el-button>
                </template>
            </el-table-column>
        </el-table>

        <div class="pagination ">
            <el-pagination
                    background
                    layout="total,prev, pager, next,jumper"
                    :page-count="res.last_page"
                    @current-change="loadData"
            >
            </el-pagination>
        </div>
        <img-show ref="imgView"></img-show>
    </div>
</template>

<script>
    import api from '../../js/api/carousel'
    import imgShow from '../../components/imgView'

    export default {
        components: {
            imgShow,
        },
        data() {
            return {
                data: {
                    id: null,
                    title: null,
                    jump_page: null,
                },
                statusOptions: [{label: '可用', value: 1}, {label: '不可用', value: 0}],
                res: {}
            }
        },
        mounted() {
            this.loadData(1);
        },
        methods: {
            loadData(page) {
                this.data.jump_page = page;
                api.carouselList(this.data, res => {
                    this.res = res.data.data;
                });
            },
            add() {
                this.$router.push({name: 'carouselAdd'})
            },
            ref() {
                this.data = {
                    id: null,
                    status: null,
                    jump_page: null,
                };
                this.loadData(1)
            },
            del(scope) {
                let _this = this;
                this.$confirm('确认删除' + scope.row.title + '?', '提示', {
                    confirmButtonText: '确定',
                    cancelButtonText: '取消',
                    type: 'warning'
                }).then(() => {
                    api.delete({id: scope.row.id}, () => {
                        this.$message.success('删除成功');
                        _this.loadData(1);
                    })
                })
            },

            handleClick(item) {
                this.$router.push({
                    name: 'carouselAdd', query: {
                        id: item.id
                    }
                })
            },
            imgPreview(src) {
                console.log(src);
                this.$refs.imgView.showImg(src);
            }
        }
    }
</script>
